<template>
  <div class='many_conetnt'>
    <div class="pd10" v-show="content">
      <mt-cell title="" is-link value="" @click.native="chooseSex"></mt-cell>
      <mt-cell title="" is-link value="" to="/me/area"></mt-cell>
      <mt-cell title="" is-link>
        <span></span>
      </mt-cell>
    </div>

    <div v-show="sexShow">
      <mt-radio
        v-model="value"
        align="right"
        :options="['', '']">
      </mt-radio>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        alphabet: [],
        sexShow: false,
        content: true
      }
    },
    created() {
    },
    methods: {
      chooseSex () {
         this.sexShow = true
         this.content = false
      }
    }
  }

</script>
<style scoped>
  .pd10 {
    padding: 10px 0;
  }
  img {
    width: 24px;
    height: 24px;
  }

</style>



